
import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import { stack } from '../assets/cv';

@Component({ name: 'tech' })
export default class extends Vue {

    public stack = stack;

    public mounted() {
        this.stack.forEach((s, j) => s.techs.forEach((t, i) => {
            setTimeout(() => this.$nextTick(() => t.skill = t.target), i * j * 400);
        }));
    }
}
